  outline: 0;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.8);
  transition: opacity .3s ease, transform .5s ease, visibility .5s;
  will-change: opacity, transform;
}

