.teaser__item-img::after {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(38, 38, 38, 0.4);
  transition: .4s ease;
  content: '';
}

.teaser__item:hover .teaser__item-img::after {
  opacity: 1;
}

.teaser__item-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  max-width: 235px;
  width: 100%;
  transition: .4s ease;
}

.teaser__item:hover .teaser__item-text {
  opacity: 1;
}

.teaser__item-title {
  margin-bottom: 10px;
  font-size: 20px;
  line-height: 25px;
  font-weight: 700;
  letter-spacing: 1.2px;
  color: #fff;
}

.teaser__title {
  margin: -5px 0 23px;
  letter-spacing: 1.8px;
  line-height: 37px;
  font-size: 30px;
}

.teaser__descr {
  font-size: 16px;
}

.suggested-prods {
  border-top: 1px solid #e8e8e8;
}

.suggested-prods__title {
  margin-bottom: 30px;
}

.portfolio + .portfolio {
  padding-top: 78px;
  border-top: 1px solid #e8e8e8;
}

@media only screen and (max-width: 767px) {
  .portfolio + .portfolio {
    padding-top: 50px;
  }
}

.portfolio__breadcrumbs {
  padding: 26px 0;
}

@media only screen and (max-width: 1023px) {
  .portfolio__row {
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (max-width: 1023px) {
  .portfolio__col:nth-child(2) {
    order: -1;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .portfolio__col:nth-child(2) {
    margin-bottom: 15px;
  }
}

.portfolio__nav .slick-list {
  margin: 0 -15px;
}

.portfolio__nav-item {
  width: 50% !important;
  padding: 0 15px;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .portfolio__nav-item {
    width: 100% !important;
  }
}

@media only screen and (max-width: 767px) {
  .portfolio__nav-item:nth-child(1) ~ .portfolio__nav-item {
    margin-top: 15px;
  }
}

.portfolio__nav-item:nth-child(2) ~ .portfolio__nav-item {
  margin-top: 30px;
}

.portfolio__nav-img {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.portfolio__nav-img:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 98.88889%;
}

.portfolio__view .slider-btn {
  margin-top: -6px;
}

.portfolio__view .slider-btn_prev {
  left: 34px;
}

@media only screen and (max-width: 767px) {
  .portfolio__view .slider-btn_prev {
    left: 15px;
  }
}

.portfolio__view .slider-btn_next {
  right: 34px;
}

@media only screen and (max-width: 767px) {
  .portfolio__view .slider-btn_next {
    right: 15px;
  }
}

.portfolio__view-img {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.portfolio__view-img:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 99.64912%;
}

.portfolio__products-title {
  margin-bottom: 20px;
}

.portfolio__text-title {
  margin-bottom: 20px;
  letter-spacing: 2.1px;
}

.portfolio__text-descr:not(:last-child) {
  margin-bottom: 25px;
}

.portfolio__date-text {
  color: #aeaeae;
  font-size: 14px;
  letter-spacing: 0.84px;
}
